/* eslint-disable indent */
import request from '@/utils/request'

/**
 * 选择项
 * @param {*} params
 */
export function getEventItems(params = {}) {
    return request({
        url: '/event/items',
        method: 'get',
        params: params
    })
}

/**
 * 獲取投票列表
 * @param {*} params
 */
export function getEventList(params = {}) {
    return request({
        url: '/event/list',
        method: 'get',
        params: params
    })
}

/**
 * 创建投票
 * @param {*} params
 */
export function createEvent(params = {}) {
    return request({
        url: '/event/create',
        method: 'post',
        data: params
    })
}

/**
 * 更新投票
 * @param {*} params
 */
export function updateEvent(id, params = {}) {
    return request({
        url: `/event/${id}/update`,
        method: 'post',
        data: params
    })
}

/**
 * 关闭投票
 * @param {*} params
 */
export function closeEvent(id, params = {}) {
    return request({
        url: `/event/${id}/close`,
        method: 'post',
        data: params
    })
}
