import { invalid } from './Validator'
import store from '../../../store'

export const formData = {
    title: null,
    author: null,
    organization: null,
    description: null,
    worksType: null,
    content: null,
    photos: [],
    tabs: [],
    remark: null,
    event: null
}

export const formItem = (options) => {
    let newOptions = JSON.parse(JSON.stringify(options))
    return {
        event: {
            label: '投票项',
            placeholder: '请选择投票项',
            invalid: invalid().invalidEvent,
            options: newOptions['events'],
            component: 'Select',
            state: true,
            required: true
        },
        title: {
            label: '名称',
            placeholder: '请填写名称',
            state: true,
            invalid: invalid().invalidTitle,
            component: 'Input',
            required: true
        },
        author: {
            label: '作者',
            placeholder: '请填写作者',
            state: false,
            component: 'Input',
            required: false
        },
        organization: {
            label: '学校（单位）',
            placeholder: '请填写学校（单位）',
            state: false,
            component: 'Input',
            required: false
        },
        worksType: {
            label: '作品类型',
            placeholder: '请选择作品类型',
            invalid: invalid().invalidWorksType,
            state: true,
            options: newOptions['worksType'],
            component: 'Select',
            required: true
        },
        description: {
            label: '说明',
            placeholder: '请填写说明',
            component: 'Textarea',
            state: false
        },
        content: {
            label: '内容',
            placeholder: '请填写内容',
            state: false,
            component: 'Editor',
            required: false
        },
        photos: {
            label: '图集',
            state: false,
            component: 'UploadImages',
            action: store.getters.setting.domain + '/api/v1/admin/upload/image',
            required: false
        },
        tabs: {
            label: '标签',
            placeholder: '请选择标签',
            options: newOptions['tabs'],
            component: 'MultiSelect',
            required: false
        },
        remark: {
            label: '备注',
            placeholder: '请填写备注',
            component: 'Textarea',
            state: false
        }
    }
}
