/* eslint-disable indent */
import request from '@/utils/request'

/**
 * 评审列表
 * @param {*} params
 */
export function getList(params = {}) {
    return request({
        url: '/review/list',
        method: 'get',
        params: params
    })
}

/**
 * 分配评审人员
 * @param {*} params
 */
export function distributionReviewBy(params = {}) {
    return request({
        url: '/review/distribution_user',
        method: 'post',
        data: params
    })
}

/**
 * 评审详情
 * @param {*} params
 */
export function reviewDetail(params = {}) {
    return request({
        url: '/review/review_detail',
        method: 'get',
        params: params
    })
}

/**
 * 提交评审
 * @param {*} id
 * @param {*} params
 */
export function updateReview(id, params = {}) {
    return request({
        url: `/review/${id}/update_review`,
        method: 'post',
        data: params
    })
}

