<template>
  <b-table
    :items="data"
    :fields="fields"
    :sort-by.sync="sortBy"
    :sort-desc.sync="sortDesc"
    :striped="true"
    :bordered="true"
    :current-page="currentPage"
    :per-page="perPage"
    responsive
    class="card-table"
  >
    <template slot="worksType" slot-scope="data">
      <span>{{data.item.worksType|getText('worksType', options)}}</span>
    </template>
    <template slot="status" slot-scope="data">
      <span>{{data.item.status|getText('status', options)}}</span>
    </template>
    <template slot="event" slot-scope="data">
      <span>{{data.item.event|getText('event', options)}}</span>
    </template>
    <template slot="tabs" slot-scope="data">
      <span>{{data.item.tabs|getText('tabs', options)}}</span>
    </template>
    <template slot="actions" slot-scope="data">
      <b-btn
        variant="default btn-xs icon-btn md-btn-flat"
        v-b-tooltip.hover
        title="編輯"
        @click="showUserEditModal(data.item)"
      >
        <i class="ion ion-md-create"></i>
      </b-btn>
      <b-dropdown variant="default btn-xs icon-btn md-btn-flat hide-arrow" :right="!isRTL">
        <template slot="button-content">
          <i class="ion ion-ios-settings"></i>
        </template>
        <b-dropdown-item href="javascript:void(0)" @click="del(data.item)">刪除</b-dropdown-item>
      </b-dropdown>
    </template>
  </b-table>
</template>

<script>
export default {
  name: 'user-table',
  data () {
    return {
      sortBy: 'id',
      sortDesc: false
    }
  },
  props: {
    fields: {
      type: Array
    },
    data: {
      type: Array
    },
    currentPage: {
      type: Number
    },
    perPage: {
      type: Number
    },
    options: {
      type: Object
    }
  },
  filters: {
    getText: function (value, type, options) {
      if (!value) return ''
      if (type === 'worksType') {
        return options.worksType.find(v => v.value === value).text
      } else if (type === 'status') {
        if (value === 'wait_review') {
          return '等待评审'
        } else if (value === 'doing_review') {
          return '正在评审'
        } else if (value === 'done_review') {
          return '评审完成'
        }
      } else if (type === 'event') {
        return options.events.find(v => v.value === value).text
      } else if (type === 'tabs') {
        var data = []
        value.forEach(element => {
          data.push(options.tabs.find(v => v.value === element).text)
        })
        if (data.length > 0) {
          return data.join(',')
        } else {
          return '-'
        }
      }
    }
  },
  methods: {
    showUserEditModal (row) {
      this.$emit('edit', row)
    },
    del (row) {
      this.$swal({
        title: '是否删除',
        type: 'warning',
        showCancelButton: true,
        confirmButtonText: this.$t('TITLE_CONFIRM'),
        cancelButtonText: this.$t('TITLE_CANCEL'),
        confirmButtonColor: '#1cbb84',
        allowOutsideClick: false
      }).then((result) => {
        if (result.value) {
          this.$emit('del', row)
        }
      })
    }
  }
}
</script>
