/* eslint-disable space-before-function-paren */
/* eslint-disable indent */
import { required } from 'vuelidate/lib/validators'

export const validator = () => {
    return {
        formData: {
            actualTickets: {
                required
            },
            fraction: {
                required
            },
            actualFraction: {
                required
            }
        }
    }
}

export const invalid = () => {
    return {
        invalidActualTickets(t) {
            if (!t.v.formData.actualTickets.required) {
                return t.$t('TITLE_REQUIRED')
            }
            return undefined
        },
        invalidFraction(t) {
            if (!t.v.formData.fraction.required) {
                return t.$t('TITLE_REQUIRED')
            }
            return undefined
        },
        invalidActualFraction(t) {
            if (!t.v.formData.actualFraction.required) {
                return t.$t('TITLE_REQUIRED')
            }
            return undefined
        }
    }
}
