/* eslint-disable indent */
import request from '@/utils/request'

/**
 * 选择项
 * @param {*} params
 */
export function getTabItems(params = {}) {
    return request({
        url: '/tab/items',
        method: 'get',
        params: params
    })
}

/**
 * 獲取标签列表
 * @param {*} params
 */
export function getTabList(params = {}) {
    return request({
        url: '/tab/list',
        method: 'get',
        params: params
    })
}

/**
 * 创建标签
 * @param {*} params
 */
export function createTab(params = {}) {
    return request({
        url: '/tab/create',
        method: 'post',
        data: params
    })
}

/**
 * 更新标签
 * @param {*} params
 */
export function updateTab(id, params = {}) {
    return request({
        url: `/tab/${id}/update`,
        method: 'post',
        data: params
    })
}

/**
 * 删除标签
 * @param {*} params
 */
export function deleteTab(id, params = {}) {
    return request({
        url: `/tab/${id}/delete`,
        method: 'post',
        data: params
    })
}
