<template>
  <b-modal
    ref="rankModal"
    :no-close-on-backdrop="true"
    :no-close-on-esc="true"
    size="lg"
    :centered="true"
    cancel-title="取消"
    ok-title="确定"
  >
    <div
      slot="modal-title"
    >排名表（已打分：{{usersData|filtersReview('done_review')}}、未打分：{{usersData|filtersReview('doing_review')}}）</div>
    <div class="row">
      <b-btn
        variant="info rounded-pill"
        class="d-block ml-2 mb-3"
        @click="handleCalRank"
      >算出排名（请确保评委已经打完分，再进行排名操作）</b-btn>
      <b-btn variant="info rounded-pill" class="d-block ml-2 mb-3" @click="handleDownloadRank">下载排名</b-btn>
      <RackTable
        :fields="fields"
        :data="usersData"
        @review="showChangeReviewModal"
        :actionTitle="'改分'"
      />
    </div>
  </b-modal>
</template>

<script>
import RackTable from '../components/RackTable'
import { calRank, updateFraction, downloadRank } from '@/api/review'
import { notify } from '@/utils'
const defaultSettings = require('../../../settings.js')

export default {
  name: 'rank-modal',
  components: {
    RackTable
  },
  props: {
    currentEvent: {
      type: Object
    },
    usersData: {
      type: Array
    },
    listQuery: {
      type: Object
    }
  },
  filters: {
    filtersReview(data, status) {
      return data.filter((v) => v.status === status).length
    }
  },
  data() {
    return {
      fields: [
        {
          label: '编号',
          key: 'no',
          sortable: true,
          tdClass: 'align-middle'
        },
        {
          label: '状态',
          key: 'status',
          sortable: true,
          tdClass: 'align-middle'
        },
        {
          label: '实际总分',
          key: 'actualFraction',
          sortable: true,
          tdClass: 'align-middle'
        },
        {
          label: '排名',
          key: 'rank',
          sortable: true,
          tdClass: 'align-middle'
        },
        {
          label: '评委',
          key: 'user',
          sortable: true,
          tdClass: 'align-middle'
        },
        {
          key: 'actions',
          label: ' ',
          tdClass: 'text-nowrap align-middle text-center'
        }
      ]
    }
  },
  methods: {
    showChangeReviewModal(row, isSave) {
      const index = this.usersData.findIndex(item => item === row)
      if (row['isEdit'] && isSave) {
        const id = row.id
        updateFraction(id, {
          actualFraction: row.actualFraction,
          rank: row.rank
        }).then((result) => {
          this.$emit('getList')
          notify(defaultSettings.successAlert, 'Success', '修改成功')
        }).catch((e) => {
          console.log(e)
        })
      }
      this.usersData[index].isEdit = !row['isEdit']
    },
    handleCalRank() {
      calRank().then(res => {
        this.$emit('getList')
      })
    },
    updateFraction(bvModalEvt) {
      bvModalEvt.preventDefault()
      this.$refs.form.onSubmit()
    },
    onSubmit() {
      console.log(this.userForm)
    },
    handleDownloadRank() {
      downloadRank({ id: this.currentEvent.id }).then(res => {
        if (res.code === 0) {
          window.open(res.data.file)
        }
      })
    }
  }
}
</script>
