<template>
  <div>
    <h4 class="font-weight-bold py-2 mb-2">作品管理</h4>
    <list-filter
      :filters="listQuery.filters"
      @action="handleFilter"
      :filterOptions="filterOptions"
    />
    <div class="row">
      <b-btn
        variant="primary rounded-pill"
        class="d-block mr-2 ml-3 mb-3"
        @click="showUserCreateModal"
      >
        <span class="ion ion-md-add"></span>&nbsp;&nbsp;新增
      </b-btn>
      <b-btn
        variant="info rounded-pill"
        class="d-block ml-2 mb-3"
        @click="handleChangeToReview"
      >当前评选进行评审</b-btn>
    </div>
    <b-card no-body>
      <b-card-body>
        <div class="row">
          <div class="col">
            <span style="font-size:18px">当前评选：{{currentEvent}}</span>
          </div>
          <div class="col">
            <b-input
              size="sm"
              placeholder="搜寻..."
              class="d-inline-block w-auto float-sm-right"
              @input="filter($event)"
            />
          </div>
        </div>
      </b-card-body>
      <div class="table-responsive">
        <ListTable
          :options="options"
          :fields="fields"
          :data="usersData"
          :currentPage="listQuery.currentPage"
          :perPage="listQuery.perPage"
          @edit="showUserEditModal"
          @del="handleDel"
        />
      </div>
      <!-- Pagination -->
      <b-card-body class="pt-0 pb-3">
        <div class="row">
          <div class="col-sm text-sm-left text-center pt-3">
            <span
              class="text-muted"
              v-if="totalItems"
            >Page {{ listQuery.currentPage }} of {{ totalPages }}</span>
          </div>
          <div class="col-sm pt-3">
            <b-pagination
              class="justify-content-center justify-content-sm-end m-0"
              v-if="totalItems"
              v-model="listQuery.currentPage"
              :total-rows="totalItems"
              :per-page="listQuery.perPage"
              size="sm"
            />
          </div>
        </div>
      </b-card-body>
      <!-- / Pagination -->
    </b-card>
    <!-- Form -->
    <form-modal
      :handleType="handleType"
      :options="options"
      :userForm.sync="userForm"
      ref="addEvent"
      @save="handleOperationUser"
    ></form-modal>
    <!-- / Form -->
  </div>
</template>

<style src="@/vendor/libs/sweet-modal-vue/sweet-modal-vue.scss" lang="scss"></style>
<script>
import ListFilter from './components/ListFilter'
import ListTable from './components/ListTable'
import FormModal from './components/FormModal'
import { notify } from '@/utils'
import { getTabItems } from '@/api/tab'
import { getEventItems } from '@/api/event'
import { getWorksList, createWorks, updateWorks, deleteWorks, changeToReview } from '@/api/works'
import { listQuery, searchKeys, fields } from './table/TableOptions'
import { formData } from './form/FormOptions'
import { filterOptions } from './filter/FilterOptions'
const defaultSettings = require('../../settings.js')

export default {
  name: 'Works',
  components: {
    ListFilter,
    ListTable,
    FormModal
  },
  computed: {
    totalItems () {
      return this.usersData.length
    },
    totalPages () {
      return Math.ceil(this.totalItems / this.listQuery.perPage)
    }
  },
  data () {
    return {
      searchKeys: searchKeys(),
      handleType: 'create',
      usersData: [],
      userForm: formData,
      listQuery: listQuery(),
      filterOptions: filterOptions(),
      fields: fields(),
      originalUsersData: [],
      options: {
        'events': [],
        'tabs': [],
        'worksType': [
          { value: null, text: '请选择作品类型' },
          {
            text: '文章',
            value: 'article'
          },
          {
            text: '图片',
            value: 'photo'
          }
        ]
      },
      currentEvent: null
    }
  },
  created () {
    this.getList()
  },
  methods: {
    getList () {
      getWorksList(this.listQuery)
        .then(res => {
          this.usersData = res.data.items
          this.currentEvent = res.data.isSelectEvent
          this.originalUsersData = res.data.items.slice(0)
        })
        .catch(e => {
          console.log(e)
        })
      getTabItems().then(res => {
        this.options.tabs = res.data
      })
      getEventItems().then(res => {
        this.options.events = res.data
      })
    },
    handleFilter () {
      this.getList()
    },
    showUserCreateModal () {
      // 新增作品
      this.userForm = formData
      this.handleType = 'create'
      this.$refs['addEvent'].$refs['form'].$v && this.$refs['addEvent'].$refs['form'].$v.$reset()
      this.$refs['addEvent'].$refs.userModal.show()
    },
    showUserEditModal (row) {
      // 編輯作品
      this.handleType = 'edit'
      this.userForm = row
      this.$refs['addEvent'].$refs['form'].$v && this.$refs['addEvent'].$refs['form'].$v.$reset()
      this.$refs['addEvent'].$refs.userModal.show()
    },
    filter (value) {
      const val = value.toLowerCase()
      const filtered = this.originalUsersData.filter(d => {
        return (
          Object.keys(d)
            .filter(k => this.searchKeys.includes(k))
            .map(k => String(d[k]))
            .join('|')
            .toLowerCase()
            .indexOf(val) !== -1 || !val
        )
      })
      this.usersData = filtered
    },
    handleOperationUser () {
      if (this.handleType === 'create') {
        this.handleCreateData()
      } else {
        this.handleUpdateData()
      }
    },
    // 保存作品
    handleCreateData () {
      createWorks(this.userForm).then((result) => {
        this.usersData.unshift(result.data)
        this.$refs['addEvent'].$refs.userModal.hide()
        notify(defaultSettings.successAlert, 'Success', '创建成功')
      }).catch((e) => {
        console.log(e)
      })
    },
    // 更新作品
    handleUpdateData () {
      const id = this.userForm.id
      updateWorks(id, this.userForm).then((result) => {
        this.userForm = result.data
        this.getList()
        this.$refs['addEvent'].$refs.userModal.hide()
        notify(defaultSettings.successAlert, 'Success', '更新成功')
      }).catch((e) => {
        console.log(e)
      })
    },
    // 删除作品
    handleDel (row) {
      deleteWorks(row.id, row).then((result) => {
        notify(defaultSettings.successAlert, 'Success', '删除成功')
        this.getList()
      }).catch((e) => {
        console.log(e)
      })
    },
    handleChangeToReview() {
      var that = this
      this.$swal({
        title: '是否进行评审',
        type: 'warning',
        showCancelButton: true,
        confirmButtonText: this.$t('TITLE_CONFIRM'),
        cancelButtonText: this.$t('TITLE_CANCEL'),
        confirmButtonColor: '#1cbb84',
        allowOutsideClick: false
      }).then((result) => {
        if (result.value) {
          // 请求api
          changeToReview().then(res => {
            notify(defaultSettings.successAlert, 'Success', '当前评选中的 ' + res.data.count + ' 个作品进入评审阶段')
            that.getList()
          })
        }
      })
    }
  }
}
</script>
