/* eslint-disable indent */
import request from '@/utils/request'

/**
 * 獲取作品列表
 * @param {*} params
 */
export function getWorksList(params = {}) {
    return request({
        url: '/works/list',
        method: 'get',
        params: params
    })
}

/**
 * 创建作品
 * @param {*} params
 */
export function createWorks(params = {}) {
    return request({
        url: '/works/create',
        method: 'post',
        data: params
    })
}

/**
 * 更新作品
 * @param {*} params
 */
export function updateWorks(id, params = {}) {
    return request({
        url: `/works/${id}/update`,
        method: 'post',
        data: params
    })
}

/**
 * 删除作品
 * @param {*} params
 */
export function deleteWorks(id, params = {}) {
    return request({
        url: `/works/${id}/delete`,
        method: 'post',
        data: params
    })
}

/**
 * 作品转为评审阶段
 * @param {*} params
 */
export function changeToReview(params = {}) {
    return request({
        url: '/works/change_review',
        method: 'post',
        data: params
    })
}
