<template>
  <b-modal
    ref="userModal"
    v-on:ok="addUser"
    :no-close-on-backdrop="true"
    :no-close-on-esc="true"
    size="lg"
    :centered="true"
    cancel-title="取消"
    ok-title="保存"
  >
    <div slot="modal-title">打分（编号：{{userForm.no}}）</div>
    <div class="row">
      <div class="col">
        <h5>作品名称：{{userForm.title}}</h5>
        <div>作者：{{userForm.author}}</div>
        <div>学校（单位）：{{userForm.organization}}</div>
        <div>{{userForm.tabs}}</div>
        <div
          v-html="userForm.content"
          style="padding: 10px;margin: 10px 0;background-color: #f5f5f5;"
        ></div>
      </div>
      <div class="col-3">
        <form-type
          :formData="userForm"
          :handleType="handleType"
          :options="options"
          @save="onSubmit"
          ref="form"
        />
      </div>
    </div>
  </b-modal>
</template>

<script>
import FormType from '../form/FormType'

export default {
  name: 'form-modal',
  components: {
    FormType
  },
  props: {
    handleType: {
      type: String
    },
    userForm: {
      type: Object
    },
    options: {
      type: Object
    }
  },
  methods: {
    addUser (bvModalEvt) {
      bvModalEvt.preventDefault()
      this.$refs.form.onSubmit()
    },
    onSubmit () {
      this.$nextTick(() => {
        this.$emit('save')
      })
    }
  }
}
</script>
