<template>
  <b-table
    :items="data"
    :fields="fields"
    :sort-by.sync="sortBy"
    :sort-desc.sync="sortDesc"
    :striped="true"
    :bordered="true"
    :current-page="currentPage"
    :per-page="perPage"
    responsive
    class="card-table"
    :sticky-header="true"
  >
    <template slot="description" slot-scope="data">
      <span v-if="data.item.description">{{data.item.description}}</span>
      <span v-else>-</span>
    </template>
    <template slot="user" slot-scope="data">
      <span v-if="data.item.user">{{data.item.user}}</span>
      <span v-else>-</span>
    </template>
    <template slot="status" slot-scope="data">
      <span>{{data.item.status|getText('status')}}</span>
    </template>
    <template slot="actions" slot-scope="data">
      <b-btn
        variant="default btn-xs md-btn-flat"
        v-b-tooltip.hover
        :title="actionTitle"
        @click="showUserEditModal(data.item)"
      >
        <i class="ion ion-md-create"></i>
        {{actionTitle}}
      </b-btn>
    </template>
  </b-table>
</template>

<script>
export default {
  name: 'user-table',
  data () {
    return {
      sortBy: 'id',
      sortDesc: false
    }
  },
  props: {
    fields: {
      type: Array
    },
    data: {
      type: Array
    },
    currentPage: {
      type: Number
    },
    perPage: {
      type: Number
    },
    actionTitle: {
      type: String
    }
  },
  filters: {
    getText: function (value, type) {
      if (!value) return ''
      if (type === 'status') {
        if (value === 'wait_review') {
          return '等待评审'
        } else if (value === 'doing_review') {
          return '正在评审'
        } else if (value === 'done_review') {
          return '评审完成'
        }
      }
    }
  },
  methods: {
    showUserEditModal (row) {
      this.$emit('review', row)
    },
    del (row) {
      this.$swal({
        title: '是否删除',
        type: 'warning',
        showCancelButton: true,
        confirmButtonText: this.$t('TITLE_CONFIRM'),
        cancelButtonText: this.$t('TITLE_CANCEL'),
        confirmButtonColor: '#1cbb84',
        allowOutsideClick: false
      }).then((result) => {
        if (result.value) {
          this.$emit('del', row)
        }
      })
    }
  }
}
</script>
