<template>
  <div>
    <h4 class="font-weight-bold py-2 mb-2">评审</h4>
    <list-filter
      :filters="listQuery.filters"
      @action="handleFilter"
      :filterOptions="filterOptions"
    />
    <div class="row">
      <b-btn variant="primary rounded-pill" class="d-block mr-2 ml-3 mb-3" @click="showRankModal">
        <span class="ion ion-md-cellular"></span>&nbsp;&nbsp;排名表
      </b-btn>
      <b-btn
        variant="info rounded-pill"
        class="d-block ml-2 mb-3"
        @click="handleDistributionReviewBy"
      >分配评审</b-btn>
    </div>
    <b-card no-body>
      <b-card-body>
        <div class="row">
          <div class="col">
            <span style="font-size:18px">当前评选：{{currentEvent.title}}</span>
          </div>
          <div class="col">
            <b-input
              size="sm"
              placeholder="搜寻..."
              class="d-inline-block w-auto float-sm-right"
              @input="filter($event)"
            />
          </div>
        </div>
      </b-card-body>
      <div class="table-responsive">
        <ListTable
          :fields="fields"
          :data="usersData"
          :currentPage="listQuery.currentPage"
          :perPage="listQuery.perPage"
          :actionTitle="'打分'"
          @review="showReviewModal"
        />
      </div>
      <!-- Pagination -->
      <b-card-body class="pt-0 pb-3">
        <div class="row">
          <div class="col-sm text-sm-left text-center pt-3">
            <span
              class="text-muted"
              v-if="totalItems"
            >Page {{ listQuery.currentPage }} of {{ totalPages }}</span>
          </div>
          <div class="col-sm pt-3">
            <b-pagination
              class="justify-content-center justify-content-sm-end m-0"
              v-if="totalItems"
              v-model="listQuery.currentPage"
              :total-rows="totalItems"
              :per-page="listQuery.perPage"
              size="sm"
            />
          </div>
        </div>
      </b-card-body>
      <!-- / Pagination -->
    </b-card>
    <!-- Form -->
    <form-modal
      :handleType="handleType"
      :options="options"
      :userForm.sync="userForm"
      ref="addEvent"
      @save="handleOperationUser"
    ></form-modal>
    <!-- / Form -->
    <rank-modal
      :currentEvent="currentEvent"
      :usersData.sync="usersData"
      :listQuery="listQuery"
      @getList="getList"
      ref="rank"
    ></rank-modal>
  </div>
</template>

<style src="@/vendor/libs/sweet-modal-vue/sweet-modal-vue.scss" lang="scss"></style>
<script>
import ListFilter from './components/ListFilter'
import ListTable from './components/ListTable'
import FormModal from './components/FormModal'
import RankModal from './components/RankModal'
import { notify } from '@/utils'
import { getList, distributionReviewBy, updateReview } from '@/api/review'
import { listQuery, searchKeys, fields } from './table/TableOptions'
import { formData } from './form/FormOptions'
import { filterOptions } from './filter/FilterOptions'
const defaultSettings = require('../../settings.js')

export default {
  name: 'Review',
  components: {
    ListFilter,
    ListTable,
    FormModal,
    RankModal
  },
  computed: {
    totalItems () {
      return this.usersData.length
    },
    totalPages () {
      return Math.ceil(this.totalItems / this.listQuery.perPage)
    }
  },
  data () {
    return {
      searchKeys: searchKeys(),
      handleType: 'create',
      usersData: [],
      userForm: formData,
      listQuery: listQuery(),
      filterOptions: filterOptions(),
      fields: fields(),
      originalUsersData: [],
      options: {},
      currentEvent: {
        id: null,
        title: null
      }
    }
  },
  created () {
    this.getList()
  },
  methods: {
    getList () {
      getList(this.listQuery)
        .then(res => {
          this.usersData = res.data.items
          this.currentEvent = res.data.isSelectEvent
          this.originalUsersData = res.data.items.slice(0)
        })
        .catch(e => {
          console.log(e)
        })
    },
    handleFilter () {
      this.getList()
    },
    showRankModal() {
      this.$refs['rank'].$refs.rankModal.show()
    },
    handleDistributionReviewBy() {
      var that = this
      this.$swal({
        title: '是否分配评委',
        type: 'warning',
        showCancelButton: true,
        confirmButtonText: this.$t('TITLE_CONFIRM'),
        cancelButtonText: this.$t('TITLE_CANCEL'),
        confirmButtonColor: '#1cbb84',
        allowOutsideClick: false
      }).then((result) => {
        if (result.value) {
          // 请求api
          distributionReviewBy().then(res => {
            notify(defaultSettings.successAlert, 'Success', '当前评选中的 ' + res.data.count + ' 个作品分配给了评审员')
            that.getList()
          })
        }
      })
    },
    showReviewModal (row) {
      // 打分
      this.handleType = 'review'
      this.userForm = row
      this.$refs['addEvent'].$refs['form'].$v && this.$refs['addEvent'].$refs['form'].$v.$reset()
      this.$refs['addEvent'].$refs.userModal.show()
    },
    filter (value) {
      const val = value.toLowerCase()
      const filtered = this.originalUsersData.filter(d => {
        return (
          Object.keys(d)
            .filter(k => this.searchKeys.includes(k))
            .map(k => String(d[k]))
            .join('|')
            .toLowerCase()
            .indexOf(val) !== -1 || !val
        )
      })
      this.usersData = filtered
    },
    handleOperationUser () {
      this.handleCreateData()
    },
    // 保存打分
    handleCreateData () {
      const id = this.userForm.id
      updateReview(id, { actualTickets: this.userForm.actualTickets,
        fraction: this.userForm.fraction,
        totalFraction: this.userForm.totalFraction,
        isOverWrite: this.userForm.isOverWrite,
        actualFraction: this.userForm.actualFraction,
        rank: this.userForm.rank }).then((result) => {
        this.getList()
        this.$refs['addEvent'].$refs.userModal.hide()
        notify(defaultSettings.successAlert, 'Success', '保存成功')
      }).catch((e) => {
        console.log(e)
      })
    }
  }
}
</script>
